header {
  position: absolute;
  top: 0;
  left: 0;
  padding: 12px 5%;
  width: 100%;
  height: 56px;
  background: var(--dark-gradient);
  display: flex;
  align-items: center;
  justify-content: space-between;
  #logoCentre {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
.sound {
  position: absolute;
  right: 200px;
  top: 16px;
  cursor: pointer;
}
  h4 {
    margin-left: -10%;
    display: inline;
    font-size: 16px;
    line-height: 23px;
    color: var(--text-color);
    opacity: 0.7;
    flex-grow: 2;
  }

  nav {
    display: inline;
    z-index: 2;
    width: 15%;
    a.navbar-brand {
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
      align-items: center;
      font-stretch: expanded;
      line-height: 23px;
      letter-spacing: 0.06em;
      color: var(--gold);
      width: 100%;

      img {
        margin-right: 1em;
        width: 100%;
      }

      span {
        color: var(--text-color);
        font-weight: bold;
      }
    }
  }
  .ava {
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      transform: scale(1.1);
    }
  }
}

